import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { FormControl, InputLabel, MenuItem, Select } from "@material-ui/core";
import Translation from "../util/Translation";

/**
 * With this the user can change its language
 */
const LanguageSelection = () => {
    const { i18n } = useTranslation();
    const [selectedLang, setSelectedLang] = useState<string>(i18n.language);
    const changeLanguage = (lang: string): void => {
        console.log("changed lang to ", lang);

        setSelectedLang(lang);
        i18n.changeLanguage(lang);
    };

    return (
        <FormControl variant="outlined">
            <InputLabel id="demo-simple-select-outlined-label">
                <Translation>selectLang</Translation>
            </InputLabel>

            <Select
                labelId="demo-simple-select-outlined-label"
                id="demo-simple-select-outlined"
                value={selectedLang}
                displayEmpty
                onChange={(e) => changeLanguage(e.target.value as string)}
                label={<Translation>selectLang</Translation>}
                variant="outlined"
            >
                <MenuItem key={"de-DE"} value={"de-DE"}>
                    Deutsch
                </MenuItem>{" "}
                <MenuItem key={"en-US"} value={"en-US"}>
                    English
                </MenuItem>
            </Select>
        </FormControl>
    );
};
export default LanguageSelection;
