import React from "react";
import { Checkbox, FormControlLabel, IconButton, Popover } from "@material-ui/core";
import { useEffect, useRef, useState } from "react";
import { ISettings } from "../interfaces/ICookies";
import SettingsIcon from "@material-ui/icons/Settings";
import { useLocalStorage } from "../util/localStorage";
import LanguageSelection from "./LanguageSelection";
import Translation from "../util/Translation";
import { useTranslation } from "react-i18next";

const Settings = () => {
    const [areSettingsOpen, setAreSettingsOpen] = useState<boolean>(false);
    const { t } = useTranslation();
    const [settings, setSettings] = useLocalStorage<ISettings>("settings");
    const settingsButton = useRef<HTMLDivElement>(null);

    const [useDangerouslySetInnerHTML, setUseDangerouslySetInnerHTML] = useState<boolean>(
        settings?.useDangerouslySetInnerHTML ?? true
    );

    useEffect(() => {
        let newSettings = settings ?? {};
        newSettings = { ...newSettings };
        newSettings.useDangerouslySetInnerHTML = useDangerouslySetInnerHTML;
        setSettings(newSettings);
    }, [useDangerouslySetInnerHTML]);
    return (
        <>
            <div ref={settingsButton}>
                <IconButton
                    aria-label={t("settings")}
                    aria-controls="menu-appbar"
                    aria-haspopup="true"
                    onClick={() => setAreSettingsOpen(!areSettingsOpen)}
                    color="inherit"
                >
                    <SettingsIcon />
                </IconButton>
            </div>
            <Popover
                anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "right",
                }}
                transformOrigin={{
                    vertical: "top",
                    horizontal: "right",
                }}
                open={areSettingsOpen}
                onClose={() => setAreSettingsOpen(!areSettingsOpen)}
                anchorEl={settingsButton.current}
            >
                <div className="settings">
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={useDangerouslySetInnerHTML}
                                onChange={(e) => setUseDangerouslySetInnerHTML(e.target.checked)}
                                name="checkedB"
                                color="primary"
                            />
                        }
                        label={<Translation>allowHTML</Translation>}
                    />
                    <LanguageSelection />
                </div>
            </Popover>
        </>
    );
};
export default Settings;
