import { toast } from "react-toastify";
import { IQuestionSets } from "../interfaces/ICookies";
import { IQuestion } from "../interfaces/IQuestion";
import { getLocalStorage, setLocalStorage } from "./localStorage";

export const importJSON = (setFileName: (param: string) => void, setQuestionSelectValue: (param: string) => void) => {
    //const [storage, setStorage] = useLocalStorage(["questions"]);

    const inputField: any = document.getElementById("file-upload");
    const files = inputField?.files;

    if (files.length <= 0) {
        return false;
    }
    if (files && files[0]) setFileName(files[0].name);

    const fr = new FileReader();

    fr.onload = function (e: any) {
        try {
            const fileNameShort = files[0].name.replace(".json", "");
            let result: IQuestion[] = JSON.parse(e.target.result);
            result = result.map((item: IQuestion, index: number) => {
                item.id = index;
                item.correct = item.correct - 1;
                return item;
            });
            setLocalStorage<IQuestionSets>("questions", {
                ...getLocalStorage<IQuestionSets>("questions"),
                [fileNameShort]: result,
            });
            setQuestionSelectValue(fileNameShort);
            toast.success("Es wurden " + result.length + " Fragen erfolgreich importiert!");
        } catch (error) {
            toast.error("Beim importieren der Fragen ist ein Fehler aufgetreten. Bitte überprüfe deine .json Datei");
        }
    };

    fr.readAsText(files.item(0));
};
