import { useEffect, useState } from "react";

export function useLocalStorage<T = any>(key: string, initialValue?: T): [T | undefined, (value: T) => void] {
    const [storedValue, setStoredValue] = useState(() => {
        try {
            return JSON.parse(localStorage.getItem(key) ?? "") as T;
        } catch (error) {
            return initialValue;
        }
    });

    const setValue = (value: T) => {
        window.localStorage.setItem(key, JSON.stringify(value));
        setStoredValue(value);

        const event = new CustomEvent<string>("storageUpdate", { detail: key });
        window.dispatchEvent(event);
    };

    useEffect(() => {
        const onStorageUpdate = (e: CustomEvent<string>) => {
            if (e.detail === key) {
                try {
                    setStoredValue(JSON.parse(localStorage.getItem(key) ?? "") as T);
                } catch (error) {}
            }
        };

        window.addEventListener("storageUpdate", onStorageUpdate as EventListener);
        return () => window.removeEventListener("storageUpdate", onStorageUpdate as EventListener);
    });

    return [storedValue, setValue];
}
export function setLocalStorage<T = any>(key: string, value: T) {
    window.localStorage.setItem(key, JSON.stringify(value));

    const event = new CustomEvent<string>("storageUpdate", { detail: key });
    window.dispatchEvent(event);
}

export function getLocalStorage<T = any>(key: string): T | undefined {
    try {
        return JSON.parse(localStorage.getItem(key) ?? "");
    } catch (error) {
        return undefined;
    }
}
export function removeLocalStorage(key: string): void {
    localStorage.removeItem(key);
    const event = new CustomEvent<string>("storageUpdate", { detail: key });
    window.dispatchEvent(event);
}
