import React, { useEffect, useState } from "react";

import {
    Button,
    Checkbox,
    FormControl,
    FormControlLabel,
    InputLabel,
    MenuItem,
    Paper,
    Select,
    TextField,
} from "@material-ui/core";
import { toast } from "react-toastify";
import { importJSON } from "./util/importJson";
import { removeLocalStorage, useLocalStorage } from "./util/localStorage";
import { IQuestionSets, ITestOptions, TestMode } from "./interfaces/ICookies";
import { useHistory } from "react-router-dom";
import Translation from "./util/Translation";

function Start() {
    const [fileName, setFileName] = useState<string>();

    const history = useHistory();
    const [testOptions, setTestOptions] = useLocalStorage<ITestOptions>("testOptions");
    removeLocalStorage("currentTest");

    const [randomOrder, setRandomOrder] = useState<boolean>(testOptions?.randomOrder ?? true);
    const [count, setCount] = useState<number>(testOptions?.count ?? 10);
    const [mode, setMode] = useState<TestMode>(testOptions?.mode ?? TestMode.Normal);
    const [availableQuestions] = useLocalStorage<IQuestionSets>("questions");
    const [questionSelectValue, setQuestionSelectValue] = useState<string>(
        testOptions?.questionSet ?? Object.keys(availableQuestions ?? {})[0] ?? ""
    );

    useEffect(() => {
        let newTestOptions = testOptions ?? {};
        newTestOptions = { mode, count, randomOrder, questionSet: questionSelectValue };

        setTestOptions(newTestOptions);
    }, [mode, randomOrder, count, questionSelectValue]);

    return (
        <div>
            <Paper elevation={3}>
                <h2 style={{ marginBottom: "1rem" }}>
                    <Translation>startHeading</Translation>
                </h2>
                <div className="startSettings">
                    <FormControl variant="outlined">
                        {availableQuestions && (
                            <InputLabel id="demo-simple-select-outlined-label">
                                <Translation>selectQuestions</Translation>
                            </InputLabel>
                        )}
                        <Select
                            labelId="demo-simple-select-outlined-label"
                            id="demo-simple-select-outlined"
                            value={questionSelectValue}
                            displayEmpty
                            onChange={(e) => setQuestionSelectValue(e.target.value as string)}
                            label={availableQuestions ? <Translation>selectQuestions</Translation> : undefined}
                            variant="outlined"
                        >
                            {availableQuestions ? (
                                Object.keys(availableQuestions).map((key) => (
                                    <MenuItem key={key} value={key}>
                                        {key}
                                    </MenuItem>
                                ))
                            ) : (
                                <MenuItem value="" disabled>
                                    <Translation>noImprtedQuestions</Translation>
                                </MenuItem>
                            )}
                        </Select>
                    </FormControl>
                    <FormControl variant="outlined">
                        {availableQuestions && (
                            <InputLabel id="demo-simple-select-outlined-label">
                                <Translation>selectMode</Translation>
                            </InputLabel>
                        )}
                        <Select
                            labelId="demo-simple-select-outlined-label"
                            id="demo-simple-select-outlined"
                            value={mode}
                            displayEmpty
                            onChange={(e) => setMode(e.target.value as TestMode)}
                            label={<Translation>selectMode</Translation>}
                            variant="outlined"
                        >
                            {Object.values(TestMode).map((value) => (
                                <MenuItem value={value} key={value}>
                                    <Translation>{value}</Translation>
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>

                    <TextField
                        id="outlined-number"
                        label={<Translation>questionCount</Translation>}
                        type="number"
                        InputLabelProps={{
                            shrink: true,
                        }}
                        value={count}
                        variant="outlined"
                        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                            setCount(parseInt(event.target.value));
                        }}
                    />

                    <div>
                        <FormControlLabel
                            control={
                                <Checkbox
                                    checked={randomOrder}
                                    onChange={(e) => {
                                        setRandomOrder(e.target.checked);
                                    }}
                                    name="checkedB"
                                    color="primary"
                                />
                            }
                            label={<Translation>randomOrderAllowed</Translation>}
                        />
                    </div>
                </div>
            </Paper>
            <Paper elevation={3}>
                <div className="fileUpload">
                    <h2 style={{ marginBottom: "1rem" }}>
                        <Translation>importHeading</Translation>
                    </h2>
                    <div className="name">
                        <Translation>filename</Translation>: {fileName ?? <Translation>noFileSelected</Translation>}
                    </div>
                    <Button
                        variant="contained"
                        component="label"
                        onChange={() => importJSON(setFileName, setQuestionSelectValue)}
                    >
                        <Translation>selectFile</Translation>
                        <input type="file" hidden id="file-upload" />
                    </Button>
                </div>
            </Paper>
            <Paper elevation={3}>
                <Button
                    variant="contained"
                    color="primary"
                    disabled={(availableQuestions ?? []).length <= 0}
                    onClick={() => {
                        if ((availableQuestions?.[questionSelectValue] ?? []).length > 0) history.push("/trainer/");
                        else {
                            if ((availableQuestions ?? []).length <= 0)
                                toast.warning(<Translation>nothingImportedWarning</Translation>);
                            else toast.warning(<Translation>nothingSelected</Translation>);
                        }
                    }}
                >
                    <Translation>startTest</Translation>
                </Button>
            </Paper>
        </div>
    );
}

export default Start;
