import {
    AppBar,
    Divider,
    IconButton,
    ListItem,
    ListItemIcon,
    ListItemText,
    SwipeableDrawer,
    Toolbar,
    Typography,
    useTheme,
} from "@material-ui/core";
import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import Settings from "./Settings";

import MenuIcon from "@material-ui/icons/Menu";
import Translation from "../util/Translation";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import SecurityIcon from "@material-ui/icons/Security";
import AlternateEmailIcon from "@material-ui/icons/AlternateEmail";

const Header = () => {
    const history = useHistory();
    const theme = useTheme();
    const [isDrawerOpen, setIsDrawerOpen] = useState<boolean>(false);
    return (
        <AppBar position="static">
            <Toolbar>
                <IconButton
                    edge="start"
                    color="inherit"
                    aria-label="menu"
                    onClick={() => setIsDrawerOpen(!isDrawerOpen)}
                >
                    <MenuIcon />
                </IconButton>
                <div onClick={() => history.push("/")} style={{ cursor: "pointer", flexGrow: 1 }}>
                    <Typography variant="h6">Klausurtrainer</Typography>
                </div>
                <Settings></Settings>
            </Toolbar>
            <SwipeableDrawer
                anchor={"left"}
                open={isDrawerOpen}
                onClose={() => setIsDrawerOpen(!isDrawerOpen)}
                onOpen={() => setIsDrawerOpen(!isDrawerOpen)}
            >
                <div onClick={() => setIsDrawerOpen(!isDrawerOpen)}>
                    <div
                        style={{
                            display: "flex",
                            alignItems: "center",
                            width: 240,
                            padding: theme.spacing(0, 1),
                            // necessary for content to be below app bar
                            ...theme.mixins.toolbar,
                            justifyContent: "flex-end",
                        }}
                    >
                        <IconButton onClick={() => setIsDrawerOpen(!isDrawerOpen)}>
                            {theme.direction === "ltr" ? <ChevronLeftIcon /> : <ChevronRightIcon />}
                        </IconButton>
                    </div>
                    <Divider />
                    <ListItem button key={"privacypolicy"} onClick={() => history.push("/privacy")}>
                        <ListItemIcon>
                            <SecurityIcon />
                        </ListItemIcon>
                        <ListItemText primary={<Translation>privacypolicy</Translation>} />
                    </ListItem>
                    <ListItem button key={"imprint"} onClick={() => history.push("/imprint")}>
                        <ListItemIcon>
                            <AlternateEmailIcon />
                        </ListItemIcon>
                        <ListItemText primary={<Translation>imprint</Translation>} />
                    </ListItem>
                </div>
            </SwipeableDrawer>
        </AppBar>
    );
};
export default Header;
