import { Button, Paper, Radio } from "@material-ui/core";
import React, { useState } from "react";
import { toast } from "react-toastify";
import { IStats, QuestionState } from "../interfaces/ICookies";
import { IQuestion } from "../interfaces/IQuestion";
import { useLocalStorage } from "../util/localStorage";
import Translation from "../util/Translation";
import ParsedText from "./ParsedText";

interface QuestionProps extends IQuestion {
    questionSet: string;
    next: (answer: string) => void;
}
const Question = ({ question, id, answers, correct, next, questionSet }: QuestionProps) => {
    const [radioState, setRadioState] = useState<string>("");
    const [checked, setChecked] = useState<boolean>(false);

    const [stats, setStats] = useLocalStorage<IStats>("stats");

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        if (!checked) setRadioState(event.target.value);
    };
    const saveAnswerState = (correct: number, given: number) => {
        const state = correct === given ? QuestionState.Correct : QuestionState.Wrong;

        const newStats = stats ?? {};
        newStats[questionSet] = stats?.[questionSet] ?? {};
        newStats[questionSet][id] = stats?.[questionSet][id] ?? { answerHistory: [], currentState: state };

        newStats[questionSet][id].currentState = state;
        newStats[questionSet][id].answerHistory.push({
            result: state,
            time: new Date().getTime(),
        });
        setStats(newStats);
    };
    return (
        <div className="question">
            <Paper elevation={3}>
                <h2>
                    <Translation>question</Translation>
                </h2>
                <ParsedText>{question}</ParsedText>
            </Paper>
            <Paper elevation={3}>
                <h2>
                    <Translation>answers</Translation>
                </h2>
                {answers.map((answer, index) => (
                    <div
                        className={
                            "answers " +
                            (checked
                                ? correct === index
                                    ? "correct"
                                    : radioState === String(index)
                                    ? "false"
                                    : ""
                                : "")
                        }
                        key={index}
                    >
                        <Radio checked={radioState === String(index)} value={index} onChange={handleChange} />

                        <ParsedText>{answer}</ParsedText>
                    </div>
                ))}
            </Paper>
            <Paper elevation={3}>
                <div className="questionNavi">
                    {checked ? (
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={() => {
                                next(radioState);
                            }}
                        >
                            <Translation>next</Translation>
                        </Button>
                    ) : (
                        <div
                            onClick={() => {
                                if (radioState === "") toast.warning("Du hast keine Antwort ausgewählt!");
                            }}
                        >
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={() => {
                                    saveAnswerState(correct, parseInt(radioState));
                                    setChecked(true);
                                }}
                                disabled={radioState === ""}
                            >
                                <Translation>check</Translation>
                            </Button>
                        </div>
                    )}
                </div>
            </Paper>
        </div>
    );
};
export default Question;
