export const sameValues = (base: any, check: any): boolean => {
    let same = true;

    if (typeof base === "object") {
        try {
            Object.keys(base).forEach((key) => {
                if (typeof base[key] === "object") {
                    same = sameValues(base[key], check[key]);
                }
                if (base[key] !== check[key]) {
                    same = false;
                }
            });
        } catch (error) {
            same = false;
        }
    } else {
        same = base === check;
    }
    return same;
};
