import { GivenAnswers } from "./IGivenAnswer";
import { IQuestion } from "./IQuestion";

export interface ICookies {
    settings?: ISettings;
    questions?: IQuestionSets;
    stats?: IStats;
    testOptions?: ITestOptions;
    currentTest?: ICurrentTest;
}
export interface IStats {
    [name: string]: {
        [id: number]: IStat;
    };
}
export interface IStat {
    answerHistory: {
        result: QuestionState;
        time: number;
    }[];
    currentState: QuestionState;
}
export enum QuestionState {
    Wrong = "wrong",
    Correct = "correct",
}
export enum TestMode {
    Normal = "normal",
    WrongOnly = "wrongOnly",
}
export interface ISettings {
    useDangerouslySetInnerHTML?: boolean;
}
export interface ITestOptions {
    mode?: TestMode;
    randomOrder?: boolean;
    count?: number;
    questionSet?: string;
}
export interface IQuestionSets {
    [name: string]: IQuestion[];
}
export interface ICurrentTest {
    questionOrder: number[];
    usedOptions: string;
    givenAnswers: GivenAnswers[];
}
