import { CircularProgress } from "@material-ui/core";
import React, { useState } from "react";
import { toast } from "react-toastify";
import { ISettings } from "../interfaces/ICookies";
import { useLocalStorage } from "../util/localStorage";
import Translation from "../util/Translation";

interface ParsedTextProps {
    children: string;
}
const ParsedText = ({ children }: ParsedTextProps) => {
    const re = /img([^)]*)\)/;
    const words = children.split(re);

    const [settings] = useLocalStorage<ISettings>("settings");
    const [isLoading, setIsLoading] = useState<number[]>([]);
    return (
        <div className={"parsedText"}>
            {words.map((word, index) => {
                if (index % 2 === 0) {
                    if (settings?.useDangerouslySetInnerHTML)
                        return <div key={index} dangerouslySetInnerHTML={{ __html: word }}></div>;
                    else return <div key={index}>{word}</div>;
                } else if (index % 2 === 1) {
                    return (
                        <div key={index + "image"}>
                            {!isLoading.includes(index) && (
                                <div className="spinner">
                                    <CircularProgress />
                                </div>
                            )}
                            <img
                                style={{ display: isLoading.includes(index) ? "inline" : "none" }}
                                onLoad={() => {
                                    setIsLoading([...isLoading, index]);
                                }}
                                onError={() => {
                                    setIsLoading([...isLoading, index]);
                                    toast.error(
                                        <div>
                                            <Translation>imageLoadError</Translation>
                                            <a
                                                href={word.substring(1, word.length)}
                                                target="_blank"
                                                rel="noopener noreferrer"
                                            >
                                                <Translation>image</Translation>
                                            </a>
                                        </div>,
                                        { autoClose: false }
                                    );
                                    console.error("Could not load image ", word.substring(1, word.length));
                                }}
                                src={word.substring(1, word.length)}
                            />
                        </div>
                    );
                }
            })}
        </div>
    );
};

export default ParsedText;
