import { Button, Paper } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import { ICurrentTest, IQuestionSets, ITestOptions } from "../interfaces/ICookies";
import { IQuestion } from "../interfaces/IQuestion";
import { generateTest } from "../util/generateTest";
import { useLocalStorage } from "../util/localStorage";
import { sameValues } from "../util/sameValues";
import Translation from "../util/Translation";
import Question from "./Question";

interface TestParams {
    progress?: string;
}

const Test = () => {
    const { progress } = useParams<TestParams>();

    const [testOptions] = useLocalStorage<ITestOptions>("testOptions");
    const [currentTest, setCurrentTest] = useLocalStorage<ICurrentTest>("currentTest");
    const [allQuestions] = useLocalStorage<IQuestionSets>("questions");
    const history = useHistory();

    const [count, setCount] = useState<number>(testOptions?.count ?? 10);
    const [test, setTest] = useState<IQuestion[]>([]);

    let progressNumber = parseInt(progress ?? "1");
    if (progressNumber >= count) history.push("/result");
    progressNumber = progressNumber >= count ? count : progressNumber;

    const [active, setActive] = useState<number>(progressNumber - 1);

    if (!testOptions?.questionSet || !allQuestions || !allQuestions[testOptions?.questionSet]) {
        return (
            <div>
                <h2>
                    <Translation>invalidQuestions</Translation>
                </h2>
                <Button onClick={() => history.push("/")} variant="contained" color="primary">
                    <Translation>backToStart</Translation>
                </Button>
            </div>
        );
    }
    useEffect(() => {
        setActive(progressNumber - 1);
    }, [progressNumber]);

    useEffect(() => {
        let test: IQuestion[];
        if (
            (currentTest?.questionOrder.length ?? 0) > 0 &&
            sameValues(JSON.parse(currentTest?.usedOptions ?? "{}"), testOptions)
        ) {
            test = currentTest?.questionOrder.map((index) =>
                allQuestions[testOptions.questionSet as string].find((question) => question.id === index)
            ) as IQuestion[];
        } else {
            test = generateTest(testOptions?.questionSet as string);
        }
        setTest(test);
        setCount(test.length);
    }, [testOptions?.count, testOptions?.mode, testOptions?.questionSet, testOptions?.randomOrder]);

    if (test.length <= 0) {
        return (
            <div>
                <h2>
                    <Translation>noValidQuestions</Translation>
                </h2>
                <div>
                    <Translation>noValidQuestionsExplination</Translation>
                </div>
                <Button onClick={() => history.push("/")} variant="contained" color="primary">
                    <Translation>backToStart</Translation>
                </Button>
            </div>
        );
    }

    return (
        <div>
            <Paper elevation={3}>
                <div className="outOf">
                    {active + 1} <Translation>outOf</Translation> {count}
                </div>
            </Paper>
            {test[active] && (
                <Question
                    questionSet={testOptions?.questionSet}
                    key={active}
                    {...test[active]}
                    next={(answer: string) => {
                        const current = currentTest?.givenAnswers ?? [];
                        current.push({ given: answer, questionIndex: active, correct: test[active]?.correct });

                        setCurrentTest({ ...currentTest, givenAnswers: current } as ICurrentTest);

                        history.push("/trainer/" + (active + 2));
                    }}
                ></Question>
            )}
        </div>
    );
};

export default Test;
