import { ICurrentTest, IQuestionSets, IStats, ITestOptions, QuestionState, TestMode } from "../interfaces/ICookies";
import { IQuestion } from "../interfaces/IQuestion";
import { getLocalStorage, setLocalStorage } from "./localStorage";
import { shuffleAnswers } from "./shuffleAnswers";

export const generateTest = (questionSet: string) => {
    const allQuestions = getLocalStorage<IQuestionSets>("questions");
    const stats = getLocalStorage<IStats>("stats")?.[questionSet];
    const settings = getLocalStorage<ITestOptions>("testOptions");

    let questions = allQuestions?.[questionSet] ?? [];
    if (settings?.mode === TestMode.WrongOnly) {
        questions = questions.filter((item) => !(stats?.[item.id]?.currentState === QuestionState.Correct));
    }

    const order: number[] = [];
    const generatedQuestions: IQuestion[] = [];

    let count = settings?.count ?? 10;

    if (count > questions.length) count = questions.length;

    for (let index = 0; index < count; index++) {
        let generatedIndex = Math.floor(Math.random() * questions.length);
        while (order.includes(generatedIndex)) {
            generatedIndex = Math.floor(Math.random() * questions.length);
        }

        order.push(generatedIndex);

        const { answers, correct } = settings?.randomOrder
            ? shuffleAnswers(questions[generatedIndex].answers, questions[generatedIndex].correct)
            : { answers: questions[generatedIndex].answers, correct: questions[generatedIndex].correct };

        generatedQuestions.push({ ...questions[generatedIndex], answers, correct });
    }

    setLocalStorage<ICurrentTest>("currentTest", {
        questionOrder: order,
        usedOptions: JSON.stringify(settings),
        givenAnswers: [],
    });

    return generatedQuestions;
};
