import { Accordion, AccordionDetails, AccordionSummary, Button, Paper, Typography } from "@material-ui/core";
import React from "react";

import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ParsedText from "./ParsedText";
import { useHistory } from "react-router-dom";
import Translation from "../util/Translation";
import { removeLocalStorage, useLocalStorage } from "../util/localStorage";
import { ICurrentTest, IQuestionSets, ITestOptions } from "../interfaces/ICookies";

const Result = () => {
    const [currentTest] = useLocalStorage<ICurrentTest>("currentTest");
    const [testOptions] = useLocalStorage<ITestOptions>("testOptions");
    const [allQuestions] = useLocalStorage<IQuestionSets>("questions");

    if (!testOptions?.questionSet || !allQuestions || !allQuestions[testOptions?.questionSet]) {
        return <div>Someting went wrong</div>;
    }
    const questions = allQuestions[testOptions?.questionSet];
    let correctAnswers = 0;
    let wrongAnswers = 0;

    const history = useHistory();
    currentTest?.givenAnswers.forEach((item) => {
        if (String(item.correct) == item.given) correctAnswers++;
        else wrongAnswers++;
    });

    const correctPercent = Math.round((correctAnswers / (correctAnswers + wrongAnswers)) * 100);
    return (
        <div className="result">
            <Paper elevation={3}>
                <h1>
                    <Translation>result</Translation>
                </h1>
                <div className="percentage">
                    <span className={"percentageNumber" + (correctPercent >= 50 ? " good" : " bad")}>
                        {correctPercent}%
                    </span>
                    <span>
                        <Translation>resultText</Translation>
                    </span>
                </div>
                <div className="count">
                    <span className="correctCount">{correctAnswers}</span>
                    <span>
                        <Translation>questionsCorrect</Translation>
                    </span>
                </div>
                <div className="count">
                    <span className="falseCount">{wrongAnswers}</span>
                    <span>
                        <Translation>questionsWrong</Translation>
                    </span>
                </div>
            </Paper>

            <div className="repeat">
                {currentTest?.givenAnswers.map((answer, index) => {
                    return (
                        <div key={answer.questionIndex}>
                            <Accordion
                                className={
                                    "restultQuestion " + (String(answer.correct) === answer.given ? "correct" : "false")
                                }
                            >
                                <AccordionSummary
                                    expandIcon={<ExpandMoreIcon />}
                                    aria-controls="panel1a-content"
                                    id="panel1a-header"
                                >
                                    <Typography>
                                        <Translation>question</Translation> {index + 1}
                                    </Typography>
                                </AccordionSummary>
                                <AccordionDetails>
                                    <div className="details">
                                        <h2>
                                            <Translation>question</Translation>
                                        </h2>
                                        <ParsedText>{questions[answer.questionIndex].question}</ParsedText>

                                        {!isNaN(parseInt(answer.given)) && (
                                            <>
                                                <h2>
                                                    <Translation>yourAnswer</Translation>
                                                </h2>
                                                <ParsedText>
                                                    {questions[answer.questionIndex].answers[parseInt(answer.given)]}
                                                </ParsedText>
                                            </>
                                        )}
                                        <h2>
                                            <Translation>correctAnswer</Translation>
                                        </h2>
                                        <ParsedText>
                                            {questions[answer.questionIndex].answers[answer.correct]}
                                        </ParsedText>
                                    </div>
                                </AccordionDetails>
                            </Accordion>
                        </div>
                    );
                })}
            </div>
            <Paper elevation={3}>
                <Button
                    variant="contained"
                    color="primary"
                    onClick={() => {
                        removeLocalStorage("currentTest");
                        history.push("/");
                    }}
                >
                    <Translation>repeat</Translation>
                </Button>
            </Paper>
        </div>
    );
};

export default Result;
